import React from 'react';
import Navigation from '../components/Navigation';
import {CopyToClipboard} from 'react-copy-to-clipboard';

const Contact = () => {
    return (
        <div className="contact">
            <Navigation/>
            <div className="contactContent">
                <div className="header"></div>
                    <div className="contactBox">
                        <h1>Contactez-moi</h1>
                        <ul>
                            <li>
                                <i className="fas fa-map-marker-alt"/>
                                <span>La Rochelle</span>
                            </li>
                            <li>
                                <i className="fas fa-mobile-alt" />
                                <CopyToClipboard text="XXXXXXXXXX">
                                    <span className="clickInput" onClick={ () => {alert('tél copié');}}>
                                        XX XX XX XX XX
                                    </span>
                                </CopyToClipboard>
                            </li>
                            <li>
                                <i className="far fa-envelope" />
                                <CopyToClipboard text="baudoux.f@gmail.com">
                                    <span className="clickInput" onClick={ () => {alert('email copié');}}>
                                    youpi@gmail.com
                                    </span>
                                </CopyToClipboard>
                            </li>
                        </ul>
                        </div>

                        <div className="socialNetwork">
                            <ul>
                                <a href="http://www.google.fr" target="_blank" rel="noopener noreferrer" >
                                    <h4>LinkedIn</h4>
                                    <i className="fab fa-linkedin" ></i>
                                </a>
                                <a href="http://www.google.fr" target="_blank" rel="noopener noreferrer" >
                                    <h4>Github</h4>
                                    <i className="fab fa-github" />
                                </a>
                                <a href="http://www.google.fr" target="_blank" rel="noopener noreferrer" >
                                    <h4>Twitter</h4>
                                    <i className="fab fa-twitter" />
                                </a>
                                <a href="http://www.google.fr" target="_blank" rel="noopener noreferrer" >
                                    <h4>Codepen</h4>
                                    <i className="fab fa-codepen" />
                                </a>
                            </ul>
                        </div>

                    
                
            </div>
        </div>
    );
};

export default Contact;